<template>
  <div class="app-container">
    <el-card class="filter-container" shadow="never">
      <div>
        <i class="el-icon-search"></i>
        <span></span>
        <el-button
          style="float:right"
          type="primary"
          @click="handleSearchList()"
          size="small">
          
        </el-button>
        <el-button
          style="float:right;margin-right: 15px"
          @click="handleResetSearch()"
          size="small">
          
        </el-button>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="listQuery" size="small" label-width="140px">
          <el-form-item label="">
            <el-input v-model="listQuery.orderSn" class="input-width" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="listQuery.receiverKeyword" class="input-width" placeholder="/"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-date-picker
              class="input-width"
              v-model="listQuery.createTime"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="listQuery.status" class="input-width" placeholder="" clearable>
              <el-option v-for="item in statusOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <!--<el-form-item label="">
            <el-select v-model="listQuery.orderType" class="input-width" placeholder="" clearable>
              <el-option v-for="item in orderTypeOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="listQuery.sourceType" class="input-width" placeholder="" clearable>
              <el-option v-for="item in sourceTypeOptions"
                         :key="item.value"
                         :label="item.label"
                         :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>-->
        </el-form>
      </div>
    </el-card>
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span></span>
    </el-card>
    <div class="table-container">
      <el-table ref="orderTable"
                :data="list"
                style="width: 100%;"
                @selection-change="handleSelectionChange"
                v-loading="listLoading" border>
        <el-table-column type="selection" width="60" align="center"></el-table-column>
        <el-table-column label="" width="80" align="center">
          <template slot-scope="scope">{{scope.row.id}}</template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">{{scope.row.orderSn}}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">{{scope.row.userName}}</template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">{{scope.row.totalAmount}}</template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">
            <el-tag size="medium" v-if="scope.row.payType == 1">{{scope.row.payType | formatPayType}}</el-tag>
            <el-tag size="medium" type="success" v-else-if="scope.row.payType == 2">{{scope.row.payType | formatPayType}}</el-tag>
            <el-tag size="medium" type="danger" v-else>{{scope.row.payType | formatPayType}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">{{scope.row.orderType | formatOrderType}}</template>
        </el-table-column>
        <el-table-column label="" width="100" align="center">
          <template slot-scope="scope">
            <el-tag size="medium" type="warning" v-if="scope.row.status == 1">{{scope.row | formatStatus}}</el-tag>
            <el-tag size="medium" v-else-if="scope.row.status == 2">{{scope.row | formatStatus}}</el-tag>
            <el-tag size="medium" type="success" v-else-if="scope.row.status == 4">{{scope.row | formatStatus}}</el-tag>
            <el-tag size="medium" type="info" v-else>{{scope.row | formatStatus}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column label="" width="120" align="center">
          <template slot-scope="scope">{{scope.row.createTime | formatCreateTime}}</template>
        </el-table-column>
        <el-table-column label="" width="200" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button
              size="mini"
              @click="handleViewOrder(scope.$index, scope.row)"
            ></el-button>
            <el-button
              size="mini"
              @click="handleCloseOrder(scope.$index, scope.row)"
              v-show="scope.row.status===0"></el-button>
            <el-button size="mini" @click="handleDeliveryOrder(scope.$index, scope.row)" v-show="scope.row.status===1 && scope.row.orderType === 0"></el-button>
            <el-button size="mini" @click="handleDeliveryOrder(scope.$index, scope.row)" v-show="scope.row.status===1 && scope.row.orderType === 1"></el-button>
            <!--<el-button
              size="mini"
              @click="handleViewLogistics(scope.$index, scope.row)"
              v-show="(scope.row.status===2||scope.row.status===3) && scope.row.orderType === 0"></el-button>-->
            <el-button
              size="mini"
              type="danger"
              @click="handleDeleteOrder(scope.$index, scope.row)"
              v-show="scope.row.status===4"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="batch-operate-container">
      <el-select
        size="small"
        v-model="operateType" placeholder="">
        <el-option
          v-for="item in operateOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-button
        style="margin-left: 20px"
        class="search-button"
        @click="handleBatchOperate()"
        type="primary"
        size="small">
        
      </el-button>
    </div>
    <div class="pagination-container">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        layout="total, sizes,prev, pager, next,jumper"
        :current-page.sync="listQuery.pageNum"
        :page-size="listQuery.pageSize"
        :page-sizes="[5,10,15]"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      title=""
      :visible.sync="closeOrder.dialogVisible" width="30%">
      <span style="vertical-align: top"></span>
      <el-input
        style="width: 80%"
        type="textarea"
        :rows="5"
        placeholder=""
        v-model="closeOrder.content">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeOrder.dialogVisible = false"> </el-button>
        <el-button type="primary" @click="handleCloseOrderConfirm"> </el-button>
      </span>
    </el-dialog>
    <logistics-dialog v-model="logisticsDialogVisible"></logistics-dialog>
  </div>
</template>
<script>
  import {fetchList,closeOrder,deleteOrder} from '@/api/order'
  import {formatDate} from '@/utils/date';
  import LogisticsDialog from '@/views/oms/order/components/logisticsDialog';
  const defaultListQuery = {
    pageNum: 1,
    pageSize: 10,
    orderSn: null,
    receiverKeyword: null,
    status: null,
    orderType: null,
    sourceType: null,
    createTime: null,
  };
  export default {
    name: "orderList",
    components:{LogisticsDialog},
    data() {
      return {
        listQuery: Object.assign({}, defaultListQuery),
        listLoading: true,
        list: null,
        total: null,
        operateType: null,
        multipleSelection: [],
        closeOrder:{
          dialogVisible:false,
          content:null,
          orderIds:[]
        },
        statusOptions: [
          // 0->1->2->3->4->5->6->7->8->
          {
            label: '',
            value: 0
          },
          {
            label: '/',
            value: 1
          },
          {
            label: '/',
            value: 2
          },
          {
            label: '',
            value: 3
          },
          {
            label: '',
            value: 4
          },
          {
            label: '',
            value: 5
          },
          {
            label: '',
            value: 6
          },
          {
            label: '',
            value: 7
          },
          {
            label: '',
            value: 8
          }
        ],
        orderTypeOptions: [
          {
            label: '',
            value: 0
          },
          {
            label: '',
            value: 1
          }
        ],
        sourceTypeOptions: [
          {
            label: 'PC',
            value: 0
          },
          {
            label: 'APP',
            value: 1
          }
        ],
        operateOptions: [
          {
            label: "",
            value: 1
          },
          {
            label: "",
            value: 2
          },
          {
            label: "",
            value: 3
          }
        ],
        logisticsDialogVisible:false
      }
    },
    created() {
      this.getList();
    },
    filters: {
      formatCreateTime(time) {
        let date = new Date(time);
        return formatDate(date, 'yyyy-MM-dd hh:mm:ss')
      },
      formatPayType(value) {
        if (value === 1) {
          return '';
        } else if (value === 2) {
          return '';
        } else {
          return '';
        }
      },
      formatOrderType(value) {
        if (value === 0) {
          return '';
        } else {
          return '';
        }
      },
      formatStatus(item) {
        let value = item.status;
        // 0->1->2->3->4->5->6->7->8->
        if (value === 0) {
          return '';
        } else if (value === 1) {
          if(item.orderType === 1){
            return ''
          }
          return '';
        } else if (value === 2) {
          if(item.orderType === 1){
            return ''
          }
          return '';
        } else if (value === 3) {
          return '';
        } else if (value === 4) {
          return '';
        } else if (value === 5) {
          return '';
        } else if (value === 6) {
          return '';
        } else if (value === 7) {
          return '';
        } else if (value === 8) {
          return '';
        } else {
          return '';
        }
      },
    },
    methods: {
      handleResetSearch() {
        this.listQuery = Object.assign({}, defaultListQuery);
      },
      handleSearchList() {
        this.listQuery.pageNum = 1;
        this.getList();
      },
      handleSelectionChange(val){
        this.multipleSelection = val;
      },
      handleViewOrder(index, row){
        this.$router.push({path:'/oms/orderDetail',query:{id:row.id}})
      },
      handleCloseOrder(index, row){
        this.closeOrder.dialogVisible=true;
        this.closeOrder.orderIds=[row.id];
      },
      handleDeliveryOrder(index, row){
        let listItem = this.covertOrder(row);
        this.$router.push({path:'/oms/deliverOrderList',query:{list:[listItem],order:row}})
      },
      handleViewLogistics(index, row){
        this.logisticsDialogVisible=true;
      },
      handleDeleteOrder(index, row){
        let ids=[];
        ids.push(row.id);
        this.deleteOrder(ids);
      },
      handleBatchOperate(){
        if(this.multipleSelection==null||this.multipleSelection.length<1){
          this.$message({
            message: '',
            type: 'warning',
            duration: 1000
          });
          return;
        }
        if(this.operateType===1){
          //
          let list=[];
          for(let i=0;i<this.multipleSelection.length;i++){
            if(this.multipleSelection[i].status===1){
              list.push(this.covertOrder(this.multipleSelection[i]));
            }
          }
          if(list.length===0){
            this.$message({
              message: '',
              type: 'warning',
              duration: 1000
            });
            return;
          }
          this.$router.push({path:'/oms/deliverOrderList',query:{list:list}})
        }else if(this.operateType===2){
          //
          this.closeOrder.orderIds=[];
          for(let i=0;i<this.multipleSelection.length;i++){
            this.closeOrder.orderIds.push(this.multipleSelection[i].id);
          }
          this.closeOrder.dialogVisible=true;
        }else if(this.operateType===3){
          //
          let ids=[];
          for(let i=0;i<this.multipleSelection.length;i++){
            ids.push(this.multipleSelection[i].id);
          }
          this.deleteOrder(ids);
        }
      },
      handleSizeChange(val){
        this.listQuery.pageNum = 1;
        this.listQuery.pageSize = val;
        this.getList();
      },
      handleCurrentChange(val){
        this.listQuery.pageNum = val;
        this.getList();
      },
      handleCloseOrderConfirm() {
        if (this.closeOrder.content == null || this.closeOrder.content === '') {
          this.$message({
            message: '',
            type: 'warning',
            duration: 1000
          });
          return;
        }
        let params = new URLSearchParams();
        params.append('ids', this.closeOrder.orderIds);
        params.append('note', this.closeOrder.content);
        closeOrder(params).then(response=>{
          this.closeOrder.orderIds=[];
          this.closeOrder.dialogVisible=false;
          this.getList();
          this.$message({
            message: '',
            type: 'success',
            duration: 1000
          });
        });
      },
      getList() {
        this.listLoading = true;
        fetchList(this.listQuery).then(response => {
          this.listLoading = false;
          this.list = response.data.list;
          this.total = response.data.total;
        });
      },
      deleteOrder(ids){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let params = new URLSearchParams();
          params.append("ids",ids);
          deleteOrder(params).then(response=>{
            this.$message({
              message: '',
              type: 'success',
              duration: 1000
            });
            this.getList();
          });
        })
      },
      covertOrder(order){
        let address=order.receiverProvince+order.receiverCity+order.receiverRegion+order.receiverDetailAddress;
        let listItem={
          orderId:order.id,
          orderSn:order.orderSn,
          receiverName:order.receiverName,
          receiverPhone:order.receiverPhone,
          receiverPostCode:order.receiverPostCode,
          address:address,
          deliveryCompany:null,
          deliverySn:null
        };
        return listItem;
      }
    }
  }
</script>
<style scoped>
  .input-width {
    width: 203px;
  }
</style>





