<template>
  <div style="margin-top: 50px">
    <el-form :model="value" :rules="rules" ref="productInfoForm" label-width="120px" style="width: 600px" size="small">
      <el-form-item label="" prop="productCategoryCode">
        <el-cascader
          v-model="selectProductCateValue"
          :options="productCateOptions">
        </el-cascader>
      </el-form-item>
      <el-form-item label="" prop="name">
        <el-input v-model="value.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="subTitle">
        <el-input v-model="value.subTitle"></el-input>
      </el-form-item>
      <el-form-item label="" prop="brandId">
        <el-select
          v-model="value.brandId"
          @change="handleBrandChange"
          placeholder="">
          <el-option
            v-for="item in brandOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input
          :autoSize="true"
          v-model="value.description"
          type="textarea"
          placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="value.productSn"></el-input>
      </el-form-item>
      <el-form-item prop="price">
        <div slot="label"><span style="color: #f56c6c;margin-right: 4px;" slot="prop">*</span>
          <el-tooltip placement="top">
            <div slot="content">
              <p>sku</p>
              <p>skusku</p>
            </div>
            <i class="el-icon-info"></i>
          </el-tooltip>
        </div>
        <el-input v-model="value.price"></el-input>
      </el-form-item>
      <el-form-item>
        <div slot="label">
          <el-tooltip placement="top">
            <div slot="content">
              <p></p>
              <p>skusku</p>
            </div>
            <i class="el-icon-info"></i>
          </el-tooltip>
        </div>
        <el-input-number v-model="value.coin" min="0"></el-input-number>
      </el-form-item>
      <!--<el-form-item>-->
        <!--<div slot="label">-->
          <!--<el-tooltip placement="top">-->
            <!--<div slot="content">-->
              <!--<p>sku</p>-->
            <!--</div>-->
            <!--<i class="el-icon-info"></i>-->
          <!--</el-tooltip>-->
        <!--</div>-->
        <!--<el-input v-model="value.originalPrice"></el-input>-->
      <!--</el-form-item>-->
      <el-form-item>
        <div slot="label">
          <el-tooltip placement="top">
            <div slot="content">
              <p>sku</p>
            </div>
            <i class="el-icon-info"></i>
          </el-tooltip>
        </div>
        <el-input v-model="value.stock"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="value.unit" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="value.weight" style="width: 300px"></el-input>
        <span style="margin-left: 20px"></span>
      </el-form-item>
      <!--<el-form-item label="">-->
        <!--<el-input v-model="value.sort"></el-input>-->
      <!--</el-form-item>-->
      <el-form-item>
        <div slot="label">
          <el-tooltip placement="top">
            <div slot="content">
              <p></p>
            </div>
            <i class="el-icon-info"></i>
          </el-tooltip>
        </div>
        <el-input v-model="value.minBuyQuantity"></el-input>
      </el-form-item>
      <el-form-item style="text-align: center">
        <el-button type="primary" size="medium" @click="handleNext('productInfoForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {fetchListWithChildren} from '@/api/productCate'
  import {getOrderSetting} from '@/api/orderSetting'
  import {fetchList as fetchBrandList} from '@/api/brand'
  import {getProduct} from '@/api/product';

  export default {
    name: "ProductInfoDetail",
    props: {
      value: Object,
      isEdit: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        hasEditCreated:false,
        //
        selectProductCateValue: [],
        productCateOptions: [],
        brandOptions: [],
        orderSetting:{},
        rules: {
          name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 2, max: 140, message: ' 2  140 ', trigger: 'blur'}
          ],
          subTitle: [{required: true, message: '', trigger: 'blur'}],
          productCategoryCode: [{required: true, message: '', trigger: 'blur'}],
          // brandId: [{required: true, message: '', trigger: 'blur'}],
          description: [{required: true, message: '', trigger: 'blur'}],
          requiredProp: [{required: true, message: '', trigger: 'blur'}],
          price: [{
            validator: (rule, value, callback) => {
              if(!!value) value = value.toString();
              if(!value || value.length <= 0){
                callback(new Error(''))
              }else if(value.split('.').length > 2){
                callback(new Error('')) //
              }else if(value == 0 || value == 0.0 || value == 0.00){
                callback(new Error('0'))
              }else{
                value = Math.round(value * Math.pow(10,2)) / Math.pow(10,2) //
                value = Number(value).toFixed(2); //
                if(value == 0 || value == 0.0 || value == 0.00)
                  value = 0.01;
                this.value.price = value;
                callback();
              }
            },
            trigger: 'blur'
          }]
        }
      };
    },
    created() {
      this.getProductCateList();
      this.getBrandList();
      this.queryCoinMoney();
    },
    computed:{
      //
      productId(){
        return this.value.id;
      }
    },
    watch: {
      productId:function(newValue){
        if(!this.isEdit)return;
        if(this.hasEditCreated)return;
        if(newValue===undefined||newValue==null||newValue===0)return;
        this.handleEditCreated();
      },
      selectProductCateValue: function (newValue) {
        console.log('selectProductCateValue', newValue);
        if (newValue && newValue.length != 0) {
          this.value.productCategoryCode = newValue[newValue.length - 1];
          this.value.productCategoryName= this.getCateNameByCode(this.value.productCategoryCode);
        } else {
          this.value.productCategoryCode = null;
          this.value.productCategoryName = null;
        }
      }
    },
    methods: {
      queryCoinMoney(){
        getOrderSetting().then(response=>{
          this.orderSetting=response.data;
        })
      },
      //
      async handleEditCreated(){
        if(this.value.productCategoryCode!=null){
          let codes = await this.getCascaderProductCateOptionsByCode(this.value.productCategoryCode);
          console.log('handleEditCreated', codes);
          this.selectProductCateValue.push(...codes);
        }
        this.hasEditCreated=true;
      },
      async getProductCateList() {
        let productCateOptions = await this.queryProductCateList(0);
        console.log('getProductCateList', productCateOptions);
        this.productCateOptions = productCateOptions;
      },
      async queryProductCateList(parentId) {
        const self = this;
        const response = await fetchListWithChildren({parentId: parentId});
        let list = response.data;
        let result = [];
        for (let i = 0; i < list.length; i++) {
          let children = [];
          if (list[i].children != null && list[i].children.length > 0) {
            for (let j = 0; j < list[i].children.length; j++) {
              let grandson = await self.queryProductCateList(list[i].children[j].id);
              if (grandson.length == 0) {
                children.push({label: list[i].children[j].name, value: list[i].children[j].code});
              } else {
                children.push({label: list[i].children[j].name, value: list[i].children[j].code, children: grandson});
              }
            }
          }
          if (children.length == 0) {
            result.push({label: list[i].name, value: list[i].code});
          } else {
            result.push({label: list[i].name, value: list[i].code, children: children});
          }
        }
        return result;
      },
      getBrandList() {
        fetchBrandList({pageNum: 1, pageSize: 100}).then(response => {
          this.brandOptions = [];
          let brandList = response.data.list;
          for (let i = 0; i < brandList.length; i++) {
            this.brandOptions.push({label: brandList[i].name, value: brandList[i].id});
          }
        });
      },
      getCateNameByCode(code){
        let name = this.queryCateNameByCode(code, this.productCateOptions);
        console.log('getCateNameByCode', code, name);
        return name;
      },
      queryCateNameByCode(code, cascaderOptions){
        for (let i = 0; i < cascaderOptions.length; i++) {
          let cur = cascaderOptions[i];
          if (cur.value === code) {
            return cur.label;
          }

          if (cur.children) {
            let name = this.queryCateNameByCode(code, cur.children);
            if (name) {
              return name;
            }
          }
        }
      },
      async getCascaderProductCateOptionsByCode(code){
        let productCateOptions = await this.queryProductCateList(0);
        console.log('getCascaderProductCateOptionsByCode', productCateOptions);
        this.productCateOptions = productCateOptions;
        let codes = this.queryCascaderProductCateOptionsByCode(code, this.productCateOptions);
        console.log('getCascaderProductCateOptionsByCode', code, codes);
        return codes;
      },
      queryCascaderProductCateOptionsByCode(code, cascaderOptions){
        let codes = [];
        for (let i = 0; i < cascaderOptions.length; i++) {
          let cur = cascaderOptions[i];
          if (cur.value === code) {
            codes.push(cur.value);
            return codes;
          }

          if (cur.children) {
            let childrenCodes = this.queryCascaderProductCateOptionsByCode(code, cur.children);
            if (childrenCodes.length != 0) {
              codes.push(cur.value);
              codes.push(...childrenCodes);
              return codes;
            }
          }
        }
        return codes;
      },
      handleNext(formName){
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //
            if(!!this.value.coin && this.value.coin > 0){
              let coinMoney = this.orderSetting.coinMoney * this.value.coin;
              if(coinMoney >= this.value.price){
                this.$alert('', '', {
                  confirmButtonText: '',
                  type: 'warning'
                });
                return;
              }
            }

            this.$emit('nextStep');
          } else {
            this.$message({
              message: '',
              type: 'error',
              duration:1000
            });
            return false;
          }
        });
      },
      handleBrandChange(val) {
        let brandName = '';
        for (let i = 0; i < this.brandOptions.length; i++) {
          if (this.brandOptions[i].value === val) {
            brandName = this.brandOptions[i].label;
            break;
          }
        }
        this.value.brandName = brandName;
      }
    }
  }
</script>

<style scoped>
</style>



