<template>
  <el-card class="form-container" shadow="never">
    <el-form :model="brand" :rules="rules" ref="brandFrom" label-width="150px">
      <el-form-item label="" prop="amount">
        <el-input v-model="brand.amount"></el-input>
        <span>{{balance}}</span>
      </el-form-item>
      <el-form-item label="" prop="accountType">
        <el-radio-group v-model="brand.accountType" size="small" @change="accountTypeChange">
          <el-radio label="0" border></el-radio>
          <el-radio label="1" border></el-radio>
        </el-radio-group>
      </el-form-item>
      <template v-if="brand.accountType == '0'">
        <el-form-item label="" prop="bankAccount">
          <el-input v-model="brand.bankAccount"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bankAccountName">
          <el-input v-model="brand.bankAccountName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bank">
          <el-input v-model="brand.bank"></el-input>
        </el-form-item>
      </template>
      <template v-if="brand.accountType == '1'">
        <el-form-item label="" prop="bankAccount">
          <el-input v-model="brand.bankAccount"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bankAccountName">
          <el-input v-model="brand.bankAccountName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="bank">
          <el-input v-model="brand.bank"></el-input>
        </el-form-item>
      </template>
      <el-form-item>
        <el-button type="primary" @click="onSubmit('brandFrom')"></el-button>
        <el-button @click="resetForm('brandFrom')"></el-button>
      </el-form-item>
    </el-form>
  </el-card>
</template>
<script>
  import {createApply, shBalance} from '@/api/shopAccount'
  const defaultBrand={
      amount: 0.00,
      bankAccount: '',
      bankAccountName: '',
      bank: '',
      accountType:'0'
  };
  export default {
    name: 'WithdrawCashApply',
    components:{},
    data() {
      return {
        brand:Object.assign({}, defaultBrand),
        rules: {
            amount: [
              {required: true, message: '', trigger: 'blur'}
            ],
            accountType:[
              {required: true, message: '', trigger: 'blur'}
            ],
            bankAccount: [
                {required: true, message: '', trigger: 'blur'}
            ],
            bankAccountName: [
                {required: true, message: '', trigger: 'blur'}
            ],
            bank: [
                {required: true, message: '', trigger: 'blur'}
            ],
        },
        balance: 0.00
      }
    },
    created() {
        this.computedBalance();
    },
    methods: {
      accountTypeChange(value){

      },
      computedBalance() {
          shBalance().then(response => {
              this.balance = response.data.balance
          });
      },
      onSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$confirm('', '', {
              confirmButtonText: '',
              cancelButtonText: '',
              type: 'warning'
            }).then(() => {
                createApply(this.brand).then(response => {
                  this.$refs[formName].resetFields();
                  this.brand = Object.assign({},defaultBrand);
                  this.$message({
                    message: '',
                    type: 'success',
                    duration:1000
                  });
                });
            });

          } else {
            this.$message({
              message: '',
              type: 'error',
              duration:1000
            });
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        this.brand = Object.assign({},defaultBrand);
      }
    }
  }
</script>
<style>
</style>





