<template>
  <div class="app-container">
    <el-card class="operate-container" shadow="never">
      <i class="el-icon-tickets"></i>
      <span></span>
    </el-card>
    <div class="table-container">
      <el-table ref="deliverOrderTable"
                style="width: 100%;"
                :data="list" border>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">{{scope.row.orderSn}}</template>
        </el-table-column>
        <el-table-column label="" width="180" align="center">
          <template slot-scope="scope">{{scope.row.receiverName}}</template>
        </el-table-column>
        <el-table-column label="" width="160" align="center">
          <template slot-scope="scope">{{scope.row.receiverPhone}}</template>
        </el-table-column>
        <el-table-column label="" width="160" align="center">
          <template slot-scope="scope">{{scope.row.receiverPostCode}}</template>
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">{{scope.row.address}}</template>
        </el-table-column>
        <template v-if="order.orderType === 0">
          <el-table-column label="" width="160" align="center">
            <template slot-scope="scope">
              <el-select placeholder=""
                         v-model="scope.row.deliveryCompany"
                         size="small">
                <el-option v-for="item in companyOptions"
                           :key="item"
                           :label="item"
                           :value="item">
                </el-option>
              </el-select>
            </template>
          </el-table-column>
          <el-table-column label="" width="180" align="center">
            <template slot-scope="scope">
              <el-input size="small" v-model="scope.row.deliverySn"></el-input>
            </template>
          </el-table-column>
        </template>
        <el-table-column v-if="order.orderType === 1" label="" width="180" align="center">
          <template slot-scope="scope">
            {{order.appointTime}}
          </template>
        </el-table-column>
      </el-table>
      <div style="margin-top: 15px;text-align: center">
        <el-button @click="cancel"></el-button>
        <el-button @click="confirm" type="primary"></el-button>
      </div>
    </div>
  </div>
</template>
<script>
  import {deliveryOrder} from '@/api/order'
  const defaultLogisticsCompanies=["","","",""];
  export default {
    name: 'deliverOrderList',
    data() {
      return {
        list:[],
        order:{},
        companyOptions:defaultLogisticsCompanies
      }
    },
    created(){
      this.list= this.$route.query.list;
      this.order= this.$route.query.order;

      console.log(this.order)
    },
    methods:{
      cancel(){
        this.$router.back();
      },
      confirm(){
        this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          deliveryOrder(this.list).then(response=>{
            this.$router.back();
            this.$message({
              type: 'success',
              message: '!'
            });
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    }
  }
</script>
<style></style>





